import React, { FC } from "react"
import { analyticsTrack } from "../utils"
import { StoreIcon } from "../assets/icons"
import { OutboundLink } from "gatsby-plugin-google-analytics"
import Bullet from "../assets/icons/bullet.svg"
import Emoji from "../assets/icons/emoji.svg"

const styles = {
  storeButton: {
    minHeight: "64px",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    textDecoration: "none",
    gap: "16px",
  },
  storeIcon: {
    width: "30px",
    marginRight: "20px",
  },

  otherParticipant: {
    height: "534px",
  },
}
interface ParticipantProps {
  participant: {
    name: string
    option1: string
    option2: string
    option3: string
    comments: string
  }
  isSecretFriend?: boolean
}

const Participant: FC<ParticipantProps> = ({
  participant,
  isSecretFriend = false,
}) => {
  return (
    <div className="participantForm">
      <form>
        <div
          className="participantCard"
          style={isSecretFriend ? {} : styles.otherParticipant}
        >
          <div className="cardTexts">
            <h4 className="participantCardTitle">
              {isSecretFriend
                ? "O que meu amigo quer ganhar"
                : participant?.name}
            </h4>
            {participant?.option1 ||
            participant?.option2 ||
            participant?.option3 ? (
              <h5 className="participantCardOptions ">Opes de presente</h5>
            ) : (
              ""
            )}
          </div>

          {participant?.option1 ||
          participant?.option2 ||
          participant?.option3 ? (
            <>
              <div className="form-group">
                <img src={Bullet} alt="" />
                <div className="participantOption">
                  {participant?.option1 || "Opo 1"}
                </div>
              </div>
              <div className="form-group">
                <img src={Bullet} alt="" />

                <div className="participantOption">
                  {participant?.option2 || "Opo 2"}
                </div>
              </div>
              <div className="form-group">
                <img src={Bullet} alt="" />
                <div className="participantOption">
                  {participant?.option3 || "Opo 3"}
                </div>
              </div>
              <h6 className="mb-3">Observaes</h6>
              <div className={`form-group ${isSecretFriend && "mb-3"} `}>
                <div className="participantOption">
                  {participant?.comments || "(comentrios)"}
                </div>
              </div>

              <div className="shopButtonContainer">
                {isSecretFriend && (
                  <OutboundLink
                    href="https://loja.papelzinho.com/"
                    className="papelzinhoShopButton"
                    style={styles.storeButton}
                    target="_blank"
                    rel="noreferrer"
                    onClick={() => {
                      analyticsTrack(
                        "loja-papelzinho-home",
                        "clique",
                        "formulrio-amigo-loja papelzinho"
                      )
                    }}
                  >
                    Loja Papelzinho
                  </OutboundLink>
                )}
              </div>
            </>
          ) : (
            <div className="participantDidntFillContainer">
              <div className="participantDidntFillContent">
                <img src={Emoji} alt="" width="40" height="40" />

                <p className="participantDidntFillTitle">
                  Esse participante ainda no preencheu a lista de presentes
                </p>
                <div className="participantDidntFillSuggestion">
                  Que tal dar um toque no pessoal!?
                </div>
              </div>

              <div className="participantDidntFillButtonContainer">
                {isSecretFriend && (
                  <OutboundLink
                    href="https://loja.papelzinho.com/"
                    className="papelzinhoShopButton"
                    style={styles.storeButton}
                    target="_blank"
                    rel="noreferrer"
                    onClick={() => {
                      analyticsTrack(
                        "loja-papelzinho-home",
                        "clique",
                        "formulrio-amigo-loja papelzinho"
                      )
                    }}
                  >
                    Loja Papelzinho
                  </OutboundLink>
                )}
              </div>
            </div>
          )}
        </div>
      </form>
    </div>
  )
}

export { Participant }
